/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"Logrinto Blog","subtitle":"Blog zum Interaction Designer an der Schule für Gestaltung Zürich","author":{"name":"Logrinto","twitter":"https://twitter.com/logrinto"}}},"markdownRemark":{"id":"/home/travis/build/logrinto/website/src/pages/articles/2022-12-31---dynamische_typografie/index.md absPath of file >>> MarkdownRemark","html":"<p><img src=\"https://i.imgur.com/THcEJHp.png\"></p>\n<p>Bei einem gleichseitigen Dreieck sind alle drei Innenwinkel zueinander kongruent und betragen jeweils 60°. Damit ist ein gleichseitiges Dreieck eine gute Darstellung für drei Dinge, die sich in perfektem Gleichgewicht befinden. Im Bezug auf Typografie verdeutlicht die Darstellung oben, dass das Zusammenspiel der Schriftgrösse, des Zeilenabstands sowie der Spaltenbreite ausschlaggebend für einen ausgewogenen Satz ist.</p>\n<p>Mehr zum perfekten Dreieck gibt es <a href=\"https://css-tricks.com/equilateral-triangle-perfect-paragraph/\"><strong>hier</strong></a></p>\n<h2>Silbentrennung</h2>\n<p>Die Silbentrennung kann im Browser mit der hyphen-Klasse beeinflusst werden. Dabei gibt es drei Möglichkeiten diese im CSS festzulegen:</p>\n<div class=\"gatsby-highlight\" data-language=\"css\">\n      <pre class=\"language-css\"><code class=\"language-css\"><span class=\"token selector\">p</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">hyphens</span><span class=\"token punctuation\">:</span> none | manual | auto\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>Zusätzlich dazu ist es möglich, mittels &#x26;shy: selber zu bestimmen, an welcher Stelle ein Wort umbrechen soll. Verdeutlicht wird dies im Code-Beispiel unten anhand der beiden Headlines, bei denen einmal mit &#x26;shy: genau definiert ist, an welcher Stelle im Wort getrennt werden soll, sowie anhand der zweiten Headline, bei welcher hyphens: auto zugeordnet ist. Ausserdem kann vor oder nach einem Wort auch mittels dem break-tag ein harter Umbruch erzwungen werden.</p>\n<iframe height=\"265\" style=\"width: 100%;\" scrolling=\"no\" title=\"hyphens\" src=\"//codepen.io/wschoeck/embed/vwqpQY/?height=265&theme-id=0&default-tab=html,result\" frameborder=\"no\" allowtransparency=\"true\" allowfullscreen=\"true\">\n  See the Pen <a href='https://codepen.io/wschoeck/pen/vwqpQY/'>hyphens</a> by wschoeck\n  (<a href='https://codepen.io/wschoeck'>@wschoeck</a>) on <a href='https://codepen.io'>CodePen</a>.\n</iframe>\n<h2>Grösse</h2>\n<p>Es gibt verschiedene Möglichkeiten die Schriftgrösse zu definieren. Entweder kann diese in em, rem oder auch pixeln angegeben werden. Um eine optimale Schriftgrösse in einem responsiven Layout zu erhalten macht es Sinn die Typografie mit einem relativen Wert zu definieren, also entweder em oder aber rem. Letzteres bezieht sich auf die im body definierte Schriftgrösse und eignet sich gut, um die unterschiedlichen Grössen an einer Stelle im Code anzupassen.</p>\n<p>Die aktuelle Schriftgrösse kann dann entweder dynamisch mit calc oder aber anhand von breakpoints angegeben werden.</p>\n<iframe height=\"265\" style=\"width: 100%;\" scrolling=\"no\" title=\"calc\" src=\"//codepen.io/wschoeck/embed/MdGezE/?height=265&theme-id=0&default-tab=html,result\" frameborder=\"no\" allowtransparency=\"true\" allowfullscreen=\"true\">\n  See the Pen <a href='https://codepen.io/wschoeck/pen/MdGezE/'>calc</a> by wschoeck\n  (<a href='https://codepen.io/wschoeck'>@wschoeck</a>) on <a href='https://codepen.io'>CodePen</a>.\n</iframe>\n<h2>Zeilenabstand</h2>\n<p>Wenn sich die Screengrösse und somit auch die Schriftgrösse ändert, muss natürlich auch der Zeilenabstand berücksichtigt werden. Sinnvoll ist daher, diesen ebenfalls in einer relativen Grösse (z. B. mit rem oder in Prozent anzugeben), damit sich dieser mit der Schriftgrösse mitskaliert.</p>\n<p>Mehr zum Zeilenabstand im CSS gibt es <a href=\"https://www.mediaevent.de/css/line-height.html\"><strong>hier</strong></a></p>\n<h2>Spalten</h2>\n<p>Während sich die Schriftgrösse und der Zeilenabstand sowohl auf die Breite wie auch die Höhe eines Layouts beziehen, hat die Spaltenbtreite in erster Linie mit der Breite eines Screens zu tun. Obwohl sich der Spaltenraster bis zu einem gewissen Grad in der Breite auch dynamisch skaliert macht es hier oft Sinn anstelle der Spaltenbreite die Anzahl der Spalten je nach Screengrösse zu ändern.</p>\n<p>Überlegungen zur optimalen Spaltenbreite gibt es <a href=\"https://www.typolexikon.de/satzbreite/\"><strong>hier</strong></a></p>\n<h2>Fazit</h2>\n<p>Neben der richtigen Schriftwahl, der Laufweite und vielen weiteren Punkten ist für gute Typografie auch das Verhältnis zwischen Schriftgrösse, Zeilenabstand sowie der Spaltenbreite entscheidend. Um bei der digitalen Anwendung von Schrift auf alle möglichen Device-Grössen einzugehen ist es von Vorteil, diese drei Werte in relativen Grössen zu definiert. Trotz oder gerade wegen den dynamischen Inhalten sollte man aber Versuchen möglichst viele Anwendungsfälle bei der Gestaltung des Satzes bereits zu berücksichtigen und diese mit Tricks und Kniffs bei der Programmierung einzukalkulieren.</p>","fields":{"tagSlugs":["/tags/responsive-gestaltung/","/tags/typografie/","/tags/hyphens/"]},"frontmatter":{"title":"Alles bleibt anders – dynamische Typografie in digitalen Anwendungen","author":"Natasha, Wolfgang","category":"Responsive Gestaltung","tags":["Responsive Gestaltung","Typografie","hyphens"],"date":"2019-06-12","description":"Gerade den Silbentrennungen wird beim Gestalten einer Website häufig keine Aufmerksamkeit geschenkt, da davon ausgegangen wird, dass diese im Web nicht gross definiert werden können. Nachfolgend sollen darum einige Möglichkeiten aufgezeigt werden, wie auch bei digitalen Anwendungen auf die Typografie Einfluss genommen werden kann.","featuredImage":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":3,"src":"/static/header-img-135808eaccfc1094bafe8660fe5b7dcc-ef3a8.png","srcSet":"/static/header-img-135808eaccfc1094bafe8660fe5b7dcc-e0a0e.png 750w,\n/static/header-img-135808eaccfc1094bafe8660fe5b7dcc-0bb6d.png 1500w,\n/static/header-img-135808eaccfc1094bafe8660fe5b7dcc-ef3a8.png 3000w","sizes":"(max-width: 3000px) 100vw, 3000px"}}}},"wordCount":{"words":519}}},"pathContext":{"slug":"/posts/dynamische-typografie/"}}

/***/ })

